.container {
    text-align: center;
    margin-top: 20px;
}

.data-container {
    display: flex;
    justify-content: center;
}

.data-box {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
}

.error {
    color: red;
}